
import util from '@/libs/util'
import modal from '@/libs/modal.mixin'
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'

import upload from 'c%/upload'
import Tag from 'c%/Tag.vue'
import SelectStudent from 'c%/SelectStudent.vue'
import SelectFile from 'c%/SelectFile.vue'
import ISwitch from 'c%/switch.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


const emptyObject = {
art_name: '',
cid: 0,
bid: 0,
sid: 0,
eid: 0,
art_desc: '',
student_artwork_attachment: []
}

export default{
mixins: [ common, globals, modal ],
directives: {
TransferDom
},
props: {
isHomework: {
type: Boolean,
default: false
},
zIndex: {
type: Number,
default: 501
}
},
components: {
Tag,
upload,
Popup,
Group,
Cell,
XInput,
ISwitch,
XTextarea,
Flexbox,
XButton,
SelectFile,
FlexboxItem,
SelectStudent
},
data () {
return {
info: util.copy(emptyObject),
tags: [],
is_class_collect: true,
}
},
watch: {
is_class_collect: function (val) {
if(val){
this.info.cid = this.info._cid
}else{
this.info.cid = 0
}
console.log('cid')
console.log(this.info.cid)
}
},
computed: {
style () {
return {
top: this.headerHeight +'px',
zIndex: this.zIndex
}
}
},
methods: {
ok () {
if(this.check()){
let params = util.copy(this.info),
[uri,method,msg] = ['student_artworks','post','添加成功！']

params.bid = this.bid$[0]
params.student_artwork_attachment = this.uploadList
params.eid = this.eid$

this.$rest(uri)
[method](params)
.success(data=>{
this.toast(msg,'success')
this.$emit('on-success')
this.close()
}).error(response=>{
this.toast(response.body.message||'操作失败','warn')
})

}
},
check () {
let info = this.info
if(info.sid==0){
this.toast('请选择学员','warn')
return false
}
if(/^\s*$/.test(info.art_name)){
this.toast('请输入作品名称','warn')
return false
}




if(/^\s*$/.test(info.art_desc)){
this.toast('请输入作品介绍','warn')
return false
}

return true
}
}
}



