
import util from '@/libs/util'
import common from '@/libs/common.mixin'
import FileItem from 'c%/FileItem.vue'
import SelectFile from 'c%/SelectFile.vue'
import Tag from 'c%/Tag.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Rater from 'vux/src/components/rater/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'


const emptyObject = {
hc_id: 0,
eid: 0,
content: '',
result_level: 0,
check_level: 2,
homework_attachment: []
}

export default{
mixins: [common],
components: {
Tag,
Tab,
Cell,
Rater,
Group,
XButton,
TabItem,
XTextarea,
Flexbox,
FileItem,
FlexboxItem,
SelectFile
},
props: {
mode: {
type: String,
default: 'add'
},
hcId: {
type: [String,Number],
default: 0
},
homework: {
type: Object,
default: ()=>{
return {}
}
}
},
data () {
return {
tab_index: 0,
tab_list: ['精批','普批'],
standard_levels: [
{id:4,name: 'A'},
{id:3,name: 'B'},
{id:2,name: 'C'},
{id:1,name: 'D'},
],
normal_levels: [
{id:10,name: 'A+'},
{id:9,name: 'A'},
{id:8,name: 'A-'},
{id:7,name: 'B+'},
{id:6,name: 'B'},
{id:5,name: 'B-'},
{id:4,name: 'C+'},
{id:3,name: 'C'},
{id:2,name: 'C-'},
{id:1,name: 'D'},
],
info: util.copy(emptyObject)
}
},
methods: {
publish () {
this.$emit('on-publish')
},
addToArtwork () {
this.$emit('add-artwork')
},
submit_correct () {
this.info.check_level = this.tab_index + 1
this.info.hc_id = this.hcId
this.info.eid = this.eid$
this.info.bid = this.homework.bid

console.log('INFO')
console.log(util.copy(this.info))

this.$rest('homework_completes/'+this.hcId+'/homework_reply')
.post(this.info)
.success(data=>{
this.toast('提交成功','success')
this.$emit('on-success')
this.cancel()
}).error(response=>{
this.toast(response.body.message||'提交失败','warn')
})
},
cancel () {
this.$emit('close')
}
},
computed: {
level_img () {
let [standard_level,homework,img] = [{1:'A',2:'B',3:'C',4:'D'},this.homework,'/assets/images/A.png']
let normal_level = {1:'A+',2:'A',3:'A-',4:'B+',5:'B',6:'B-',7:'C+',8:'C',9:'C-',10:'D'}
if(homework.is_check===1){
if(homework.check_level===2){
img = '/static/img/neza/org/'+normal_level[homework.result_level]+'.png'
}else{
img = '/static/img/neza/org/'+standard_level[homework.result_level]+'.png'
}
}
return img
},
disabled () {
return this.info.result_level===0||this.info.content.length===0
},
levels () {
let [data,standard_levels,normal_levels] = [this.data,this.standard_levels,this.normal_levels]

if(this.tab_index==0){
return normal_levels
}
return standard_levels
}
}
}



