
import util from '@/libs/util'
import record from './record.mixin'
import common from '@/libs/common.mixin'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Popup from 'vux/src/components/popup/index.vue'

import FileItem from 'c%/FileItem.vue'
import SelectFile from 'c%/SelectFile.vue'

const emptyObject = {
hc_id: 0,
eid: 0,
content: '',
homework_attachment: []
}

export default{
mixins: [ record, common ],
components: {
Cell,
Popup,
Group,
XButton,
FileItem,
XTextarea,
SelectFile
},
props: {
mode: {
type: String,
default: 'add'
},
hcId: {
type: [String,Number],
default: 0
},
homework: {
type: Object,
default: ()=>{
return {}
}
}
},
data () {
return {
currentItem: {},
showRecord: false,
info: util.copy(emptyObject)
}
},
watch: {
'modal$.show': function (val) {
if(!val) {
this.homework = {}
this.info = util.copy(emptyObject)
this.hcId = 0
}
}
},
methods: {
publish () {
if(this.homework.is_publish > 0){
return
}

this.$emit('on-publish')
},
addToArtwork () {
if(this.homework.sart_id > 0){
return
}

this.$emit('add-artwork')
},
cancel () {
this.$emit('close')
},
ok () {
let list = this.info.homework_attachment

if(/^\s*$/.test(this.info.content)){
this.toast('请输入回复内容','warn')
return
}

this.post()
},
post () {
let params = util.copy(this.info)
params.bid = this.homework.bid
params.eid = this.eid$
params.hc_id = this.hcId

this.$rest('homework_completes/'+this.hcId+'/homework_reply')
.post(params)
.success(data=>{
this.toast('提交成功','success')
this.$emit('on-success')
this.cancel()
}).error(response=>{
this.toast(response.body.message||'提交失败','warn')
})

}

}
}



