
import util from '@/libs/util'
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


import Tag from 'c%/Tag.vue'
import FileItem from 'c%/FileItem.vue'
import ReplyPopup from './reply.vue'
import AvatarAndName from 'c%/AvatarAndName.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'



export default{
mixins: [ common, globals ],
directives: {
TransferDom
},
components: {
Tag,
Tab,
Cell,
Group,
XButton,
TabItem,
FileItem,
ReplyPopup,
AvatarAndName
},
data () {
return {
tab: 0,
info: {
eid: 0,
homework_complete: [],
incomplete_students: []
},
homework_complete: [],
previewShow: false,
id: this.$route.params.id,
}
},
mounted () {
this.init_data()
},
computed: {
style () {
let top = this.headerHeight+'px',
height = this.tab==0?this.headerHeight:(this.headerHeight+62),
bottom = this.tab==0?'62px':'0'

return {
height: 'calc(100% - ' + height + 'px)',
top: top,
bottom: bottom
}
}
},
methods: {
remind (item) {
let uri = util.sprintf('homework_tasks/wechat_notify?ht_id=%s&sid=%s',this.info.ht_id,item.sid)
this.$rest(uri)
.get()
.success(data=>{
this.toast('推送成功！','success')
this.downCallback()
}).error(response=>{
this.toast(response.body.message||'推送失败！','warn')
})
},
remindAll () {
this.$rest('homework_tasks/wechat_notify?ht_id='+this.info.ht_id)
.get()
.success(data=>{
this.toast('推送成功！','success')
this.downCallback()
}).error(response=>{
this.toast(response.body.message||'推送失败！','warn')
})
},
viewAndReply (item) {
this.$refs.reply
.set('hc_id',item.hc_id)
.set('homework',this.info)
.set('homework_complete',item)
.show('','add')
},
formatDate (time) {
time = time||''
let result = time.substring(5,10)
return result
},
getHCList () {
this.$rest('homework_tasks/'+this.id+'/homework_completes')
.get()
.success(data=>{
this.homework_complete = data.list
}).error(response=>{
this.$Message.error(response.body.error||'获取列表失败~')
})
},
init_data () {
let id = this.id
this.$rest('homework_tasks/'+id).get()
.success(data=>{
this.info = data
this.getHCList()
})
},
close () {
this.previewShow = false
}
}
}



