
import util from '@/libs/util'
import modal from '@/libs/modal.mixin'
import common from '@/libs/common.mixin'
import ReplyBasic from './reply-basic.vue'
import ReplyProfessional from './reply-professional.vue'
import AddArtwork from 'v%/home/artwork/add.vue'
import AddPublish from './add-publish.vue'
import Popup from 'vux/src/components/popup/index.vue'

export default{
mixins: [ common, modal ],
components: {
Popup,
AddArtwork,
AddPublish,
ReplyBasic,
ReplyProfessional
},
data () {
return {
hc_id: 0,
homework: {},
homework_complete: {},
}
},
computed: {
enable_homework_star () {
let result = 0
if(this.$store.state.gvars.configs&&this.$store.state.gvars.configs.params){
result = this.$store.state.gvars.configs.params.service.enable_homework_star
}

return result
},
mode () {

return this.homework_complete.is_check==1?'view':'add'
},
professionalMode () {
if(this.mode=='add'){
return this.enable_homework_star==1
}else{
return this.homework_complete.check_level>0
}
}
},
methods: {
publishHomework () {
let homework = util.copy(this.homework),
homework_complete = util.copy(this.homework_complete),
info = {
bid: homework.bid,
cid: homework.cid,
lid: homework.lid,
sid: homework_complete.sid,
hc_id: homework_complete.hc_id,
media_type: 0,
media_name: '',
link_url: '',
description: '',
publish_time: '',
_class_name: homework.one_class?homework.one_class.class_name:'',
_student_name: homework_complete.student?homework_complete.student.student_name:''
}

this.$refs.publish
.set('info',info)
.show('')
},
handleSuccess (field) {
if(field){
this.$set(this.homework_complete,field,1)
}
this.$emit('on-success')
},
addToArtwork () {
let homework = util.copy(this.homework),
homework_complete = util.copy(this.homework_complete),
info = {
art_name: '',
bid: 0,
cid: homework.cid,
sid: homework_complete.sid,
eid: homework.eid,
hc_id: homework_complete.hc_id,
art_desc: '',
student_artwork_attachment: homework_complete.homework_attachment,
_cid: homework.cid,
_class_name: homework.one_class?homework.one_class.class_name:'',
_student_name: homework_complete.student?homework_complete.student.student_name:''
}

this.$refs.artwork
.set('info',info)
.set('homework_complete',this.homework_complete)
.show('')
}
}
}



