
import util from '@/libs/util'
import modal from '@/libs/modal.mixin'
import common from '@/libs/common.mixin'
import Group from 'vux/src/components/group/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Popup from 'vux/src/components/popup/index.vue'


const emptyObject = {
bid: 0,
cid: 0,
lid: 0,
sid: 0,
hc_id: 0,
media_type: 0,
media_name: '',
link_url: '',
description: '',
publish_time: ''
}

export default{
mixins: [ common, modal ],
props: {
zIndex: {
type: Number,
default: 503
}
},
components: {
Card,
Cell,
Popup,
Group,
XInput,
Checker,
XButton,
Datetime,
XTextarea,
CheckerItem
},
data () {
return {
info: util.copy(emptyObject),
data: {}
}
},
mounted () {
},
methods: {
publish_homework () {
let info = this.info

if(this.info.media_name == ''){
this.toast('请输入媒体名称','warn')
return
}
if(this.info.publish_time == ''){
this.toast('请选择发布时间','warn')
return
}

this.$rest('homework_publishs')
.post(info)
.success(response=>{
this.toast('发布成功')
this.$emit('on-success')
this.close()
}).error(response=>{
this.toast(response.body.message,'text')
})
},
},
computed: {
style () {
return {
top: this.headerHeight +'px',
zIndex: this.zIndex
}
},
}
}



